import { Injectable } from '@angular/core';
import { Products  } from './products.model';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
@Injectable()

export class ItemdataService {
  products:Products[]=[ new Products('rose',231,'https://cdn.pixabay.com/photo/2018/01/08/09/58/flower-3069038_960_720.jpg','asd','red'),
  new Products('lilly',200,'https://cdn.bloomnation.com/media/catalog/product/cache/1/image/504x504/d2407d87ef9007de34139ad5c2c2f5a7/2/0/20180130070602_file_5a70c21a4a425.jpg','asd','yellow'),
  new Products('new flower',300,'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQaykrglN7CWT_9xcKxclRlB9z61s3V6Yvru-lM9nh9JNZZKscOPw','asd','blue'),];
  shopitem:Products;
  productadded = new Subject<Products[]>();
  shoppingitem = new Subject<Products>();
  constructor() { }

  addproduct(item:Products){
    this.products.push(item);
    this.productadded.next(this.products);
  }

  getproducts(){
    return this.products.slice();
  }

  getsingleproduct(id:number){
    return this.products[id];
  }

  delsingleproduct(id:number){
     this.products.splice(id,1);
     this.productadded.next(this.products);
  }

  updateitem(id:number,item:Products){
    this.products[id] = item;
  }

  addtoshop(item:Products){
    this.shopitem = item;
  }
  getshop(){
    return  this.shopitem;
  }

 


  
}



